.recherche{
  -ms-flex: 4 1;
      flex: 4 1;
  margin-top: 8%;
  width: 100%;
background: -webkit-gradient(linear, left top, right bottom, from(rgba(100, 147, 206, 0.2)),to(rgba(100, 147, 206, 0.5))) , url('./demenage.jpg') center no-repeat;
background: -webkit-linear-gradient(left top, rgba(100, 147, 206, 0.2),rgba(100, 147, 206, 0.5)) , url('./demenage.jpg') center no-repeat;
background: -o-linear-gradient(left top, rgba(100, 147, 206, 0.2),rgba(100, 147, 206, 0.5)) , url('./demenage.jpg') center no-repeat;
background: linear-gradient(to right bottom, rgba(100, 147, 206, 0.2),rgba(100, 147, 206, 0.5)) , url('./demenage.jpg') center no-repeat;
background-position: cover;
}

.rechercheFormulaire{
  width: 72%;
  margin: auto;
  height: 600px;
  color: rgb(1,63,138);
}

.notifError{
  height: 17px;
}

.envoye{
  display: inline-block;
  color: green;
  font-weight: bold;
}

.recherche>h3{
  color: rgb(1,63,138);
  margin-left: 3%;
}

.recherche>a{
  float: right;
  margin-right: 3%;
  font-size: 1.3em;
}



