#header{
	-ms-flex: 5 1;
	    flex: 5 1;
height: 73px;
width: 100%;
top: 0;
left: 0;
position: fixed;
z-index: 300;
background-color: white;
display: -ms-flexbox;
display: flex;
-ms-flex-pack: justify;
    justify-content: space-between;
padding-right: 20px;
padding-top: 5px;
color: rgb(1,63,138);
font-size: 0.8em;
font-weight: bold;
/*opacity: 0.8;*/
}
.navbar{
	margin-bottom: 0;
	background-color: white;
}
.navbar-brand{
	margin-right: 100px;
}
.logoDevaux{
	position: absolute;
	top: 0;
	left: 3%;
	background: url('logo.JPG') center fixed no-repeat;
}
.navbar-toggle{
	background-color: rgb(1,63,138);

}
.navbar-default .navbar-toggle .icon-bar {
    background-color: white;
}
.navbar-header{
	background-color: white;
}
.navbar-collapse>.navbar-nav>li>a:hover{
	background-color: rgb(1,63,138);
	color: white;
	border-radius: 5px;
}
.dropdown-menu>li>a{
	text-align: center;
}
.dropdown-menu>li>a:hover{
	background-color: rgb(1,63,138);
	color:white;
}
.navbar-collapse>.navbar-nav>li>a{
	text-align: center;
}
.menuBase{
	margin-left: 20%;
	font-family: 'Lato', sans-serif;
}
.menuBaseTablet{
	font-family: 'Lato', sans-serif;
	margin-left: 30%;

}





.menuBaseMobile{
	font-family: 'Lato', sans-serif;
}
#drop{
	margin-top: 15px;
}

.dropdown-menu>li>a{
	font-family: 'Lato', sans-serif;
}
#headerAdmin{
height: 68px;
width: 100%;
top: 0;
left: 0;
position: fixed;
z-index: 300;
background-color: #3C3F56 ;
display: -ms-flexbox;
display: flex;
-ms-flex-pack: justify;
    justify-content: space-between;
padding-right: 40px;
padding-left: 40px;
padding-top: 10px;
color: white;
font-size: 0.8em;
font-weight: bold;
/*opacity: 0.8;*/
}

#headerAdminMobile{
height: 68px;
width: 100%;

position: fixed;
z-index: 300;
background-color: #3C3F56 ;
display: -ms-flexbox;
display: flex;
-ms-flex-direction: column;
    flex-direction: column;
-ms-flex-pack: center;
    justify-content: center;
-ms-flex-align: center;
    align-items: center;

}
#headerAdminMobile>a:hover{
	text-decoration: none;
	color:white;
}
#headerAdmin>ul>li{
	list-style-type: none;
}

#headerAdmin>ul{
	-ms-flex: 2 1;
	    flex: 2 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}
#header>ul{
	-ms-flex: 2 1;
	    flex: 2 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

#header>ul>a>li{
list-style: none;
text-align: center;
line-height: 64px;
font-size: 1.1em;
border-radius: 7px;
width: 130px;
height: 64px;
/*margin-top: -20px;*/
}


#preHeader>ul>a>li:nth-child(1):hover, #preHeader>ul>a>li:nth-child(2):hover,
#preHeader>ul>a>li:nth-child(3):hover, #preHeader>ul>a>li:nth-child(4):hover,
#preHeader>ul>a>li:nth-child(5):hover{

background-color: rgb(9, 153, 214);
}

#header>ul>a>li{
	color: rgb(1,63,138);
}

#header>ul>a>li:nth-child(1):hover, #header>ul>a>li:nth-child(2):hover,
 #header>ul>a>li:nth-child(3):hover{
	background-color: rgb(1,63,138);

}
#header>ul>a:hover li{
color: white;
text-decoration: none;
}

.preHeaderElement{
	padding-top: 10px;
	width: 150px;
	text-align: center;
}

.preHeaderElement:nth-child(3){
	-ms-flex: 1.3 1;
	    flex: 1.3 1;
}

.navbar-header{
	font-family: 'Lato', sans-serif;

}
.preHeaderElement:nth-child(1){
	border-right: 1px solid black;
	/*width: 190px;*/
}

#nom{
	color: rgb(5, 144, 214);
}

#appeller>a{
	color: white;
}
#appeller:hover{
	background-color: rgb(0, 51, 112);
}

#estimer{
	padding-top: 40%;
background-color: rgb(9, 153, 214);
}

#estimer:hover{
background-color: rgb(0, 136, 193);
}

@-webkit-keyframes logoAnim{
from{-webkit-transform: rotate(0deg);transform: rotate(0deg)}
to{-webkit-transform: rotate(360deg);transform: rotate(360deg)}
}

@keyframes logoAnim{
from{-webkit-transform: rotate(0deg);transform: rotate(0deg)}
to{-webkit-transform: rotate(360deg);transform: rotate(360deg)}
}

#logoAgence{
	width: 59px;
	height: 52px;
	-webkit-animation: logoAnim 15s infinite;
	        animation: logoAnim 15s infinite;
}

#agence{
	font-size: 2em;
	margin-top: 8px;
}
#headerAdmin>a:hover{
	text-decoration: none;
	color:white;
}



