
.faireGerer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 850px;
  margin-bottom: 0;

}
.faireGererContainer{
  position: relative;
}

.faireGererMobile{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 1190px;
  margin-bottom: 0;
}
.faireGererContenu>h3{
  text-align: center;
  font-weight: bold;
  color: rgb(1,63,138);
  margin-bottom: 3%;
}
.faireGererBanniere{
  -ms-flex:1.5 1;
      flex:1.5 1;
  width: 100%;
  background: -webkit-gradient(linear, right top, left bottom, from(rgba(59, 84, 141, 0.2)),
   to(rgba(95,134, 234, 0))), url('./retraites.jpg') center
   no-repeat fixed;
  background: -webkit-linear-gradient(right top, rgba(59, 84, 141, 0.2),
   rgba(95,134, 234, 0)), url('./retraites.jpg') center
   no-repeat fixed;
  background: -o-linear-gradient(right top, rgba(59, 84, 141, 0.2),
   rgba(95,134, 234, 0)), url('./retraites.jpg') center
   no-repeat fixed;
  background: linear-gradient(to left bottom, rgba(59, 84, 141, 0.2),
   rgba(95,134, 234, 0)), url('./retraites.jpg') center
   no-repeat fixed;
   background-size: cover;
}
.faireGererBanniereMobile{
  height: 240px;
  width: 100%;
  background: -webkit-gradient(linear, right top, left bottom, from(rgba(59, 84, 141, 0.2)),
   to(rgba(95,134, 234, 0))), url('./retraites.jpg') bottom
   no-repeat;
  background: -webkit-linear-gradient(right top, rgba(59, 84, 141, 0.2),
   rgba(95,134, 234, 0)), url('./retraites.jpg') bottom
   no-repeat;
  background: -o-linear-gradient(right top, rgba(59, 84, 141, 0.2),
   rgba(95,134, 234, 0)), url('./retraites.jpg') bottom
   no-repeat;
  background: linear-gradient(to left bottom, rgba(59, 84, 141, 0.2),
   rgba(95,134, 234, 0)), url('./retraites.jpg') bottom
   no-repeat;
   background-size: cover;
}
.faireGererPapierPeint{
  padding-top: 2%;
  padding-bottom: 2%;

  -ms-flex: 2 1;

      flex: 2 1;
  width: 100%;
  background: url('papierPeint.jpg') center
  no-repeat fixed;
  background-size: cover;
}
.faireGererPapierPeintMobile{
  padding-top: 20px;
  -ms-flex: 2 1;
      flex: 2 1;
  width: 100%;
  background: url('papierPeint.jpg') center
  no-repeat fixed;
  background-size: cover;
}
.faireGererContenu{
  width: 80%;
  margin: auto;
  padding-top: 2%;
  padding-left: 4%;
  padding-right: 4%;
  background-color: white;
  -webkit-box-shadow: 1px 2px 2px silver;
          box-shadow: 1px 2px 2px silver;
  border-radius: 5px;
}
.faireGererContenuMobile{
  width: 95%;
  margin: auto;
  padding-top: 1%;
  padding-left: 4%;
  padding-right: 4%;
  padding-bottom: 1px;

  background-color: white;
  -webkit-box-shadow: 1px 2px 2px silver;
          box-shadow: 1px 2px 2px silver;
  border-radius: 5px;
}
.faireGererContenuMobile>h3{
  text-align: center;
  font-weight: bold;
  font-size: 1.5em;
  color: rgb(1,63,138);
  margin-bottom: 20px;
}



